.modal {
  vertical-align: middle;
  position: relative;
  z-index: 2;
  max-width: 300px;
  box-sizing: border-box;
  width: 90%;
  background: #fff;
  padding: 15px 30px;
  border-radius: 8px;
  box-shadow: 0 0 10px #000;
  margin: auto;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: lightskyblue;
}

.circle-check {
  font-size: 15em;
  color: #32cd32;
}

.modal p {
  font-size: 16px;
  font-weight: 700;
}

.blocker {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  z-index: 1;
  padding: 20px;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.75);
  text-align: center;
  display: flex;
}

.close-modal {
  position: absolute;
  top: -12.5px;
  right: -12.5px;
  display: block;
  width: 30px;
  height: 30px;
  text-indent: -9999px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url(data:image/png;base64,);
}
